.catalog-products {
  width: 320px;
  margin: 0 auto;

  .catalog-products__container {
    width: 280px;
    padding: 0 20px;
  }

  .catalog-products__title {
    margin: 0;
    margin-bottom: 20px;
    padding: 27px 0 41px 0;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    .fz-h2();
  }

  .catalog-products__sub-title {
    .fz-h4();
  }

  .catalog-products__description {
    .fz-cards-content();
  }

  .catalog-products__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .catalog-products__item {
    margin-bottom: 20px;
  }

  .products-card {
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    width: 280px;
    height: 209px;
    display: grid;
    grid-template-columns: 32px 77px 32px 72px 69px;
    grid-template-rows: 22px 33px 28px 17px 17px 25px 40px 25px;

    &:first-child {
      border-top: none;
    }


    .products-card__image--500g {
      grid-row: 2/8;
      grid-column: 2/3;
    }

    .products-card__image--1000g {
      grid-row: 2/8;
      grid-column: 2/3;
    }


    .products-card__title {
      .fz-h4();
      margin: 0;
      padding: 0;
      min-width: 76px;
      min-height: 41px;
      grid-row: 1/3;
      grid-column: 4/5;
      text-align: left;
    }

    .products-card__body {
      margin: 0;
      padding: 0;
      width: 142px;
      height: 44px;
      grid-row: 3/6;
      grid-column: 4/6;
    }


    .products-card__row--1,
    .products-card__row--2,
    .products-card__row--3 {
      margin: 0;
      padding: 0;
    }

    .products-card__parameter--left {
      text-align: left;
    }

    .products-card__parameter--right {
      text-align: right;
    }

    .products-card__button {
      grid-column: 1/6;
      grid-row: 7/8;
      width: 100%;
      padding: 10px;
      box-sizing: border-box;
    }


    .products-card__row {
      .fz-cards-content();
    }

    .products-card__sub-title {
      .fz-h4();
    }

    .products-card__description {
      margin: 0;
      padding: 0;
      .fz-cards-content;
      margin-bottom: 21px;
    }
  }

  .products-card--more {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &::before {
      position: absolute;
      left: 125px;
      top: 20px;
      content: "";
      width: 30px;
      height: 2px;
      background-color: #d3d3d3;
      cursor: pointer;
    }

    &::after {
      position: absolute;
      left: 125px;
      top: 20px;
      content: "";
      width: 30px;
      height: 2px;
      background-color: #d3d3d3;
      transform: rotate(90deg);
      cursor: pointer;
    }

    & .products-card__button {
      height: 40px;
    }
  }
}

@media (min-width: @tablet-width) {
  .catalog-products {
    width: 768px;
    margin: 0 auto;

    .catalog-products__container {
      width: 708px;
      padding: 0 30px;
    }

    .catalog-products__title {
      border: none;
      text-align: left;
      margin-left: 38px;
    }

    .catalog-products__list {
      display: flex;
      flex-wrap: wrap;
      column-gap: 60px;
      row-gap: 60px;
    }

    .catalog-products__item {
      min-height: 511px;
      width: 324px;
      display: flex;
      justify-content: center;
    }


    .catalog-products__item--gray {
      background: linear-gradient(#ffffff 15%, #f2f2f2 15%);
    }

    .products-card {
      width: 250px;
      display: flex;
      flex-direction: column;
      align-items: center;
      min-height: 464px;
      border: none;


      .products-card__image--500g {
        margin-top: 30px;
        width: 161px;
        height: 172px;
        margin-bottom: 34px;
      }

      .products-card__image--1000g {
        width: 168px;
        height: 200px;
        margin-bottom: 34px;
      }

      .products-card__title {
        .fz-h4(@size:20px; @height:24px);
        width: 154px;
        text-align: center;
        margin-bottom: 21px;
      }

      .products-card__body {
        width: 100%;
        margin-bottom: 33px;
      }


      .products-card__parameter {
        .fz-content(@size: 16px; @height: 16px);
        padding: 5px 0 5px 0;
      }
    }

    .products-card--more {
      margin: 0;
      margin-top: 81px;
      width: 340px;
      padding: 0 30px;
      outline: 2px solid #f2f2f2;
      min-height: 430px;
      flex-direction: column;
      justify-content: flex-start;

      &::before {
        position: absolute;
        left: 132px;
        top: 70px;
        content: "";
        width: 60px;
        height: 2px;
        background-color: #d3d3d3;
        cursor: pointer;
      }

      &::after {
        position: absolute;
        left: 132px;
        top: 70px;
        content: "";
        width: 60px;
        height: 2px;
        background-color: #d3d3d3;
        transform: rotate(90deg);
        cursor: pointer;
      }

      .products-card__sub-title {
        .fz-h4(@size: 20px; @height: 24px);
        margin-top: 160px;
        margin-bottom: 23px;
        width: 134px;
        text-align: center;
      }

      .products-card__description {
        width: 167px;
        .fz-content(@size: 16px; @height: 18px);
        margin-bottom: 77px;
        flex: 0 1 auto;
      }
    }
  }
}


@media (min-width: @desktop-width) {
  .main-header .main-nav .main-nav__item--activ--green {
    border-bottom: 2px solid #68b738;
  }

  .catalog-products {
    width: 1440px;
    margin: 0 auto;
    margin-top: 70px;

    .catalog-products__container {
      width: 1220px;
      padding: 0 110px;
      display: flex;
      flex-wrap: wrap;
    }

    .catalog-products__list {
      display: flex;
      flex-wrap: wrap;
      column-gap: 80px;
      row-gap: 63px;
      margin-bottom: 76px;
    }

    .catalog-products__item {
      width: 245px;
      height: 511px;
    }

    .products-card--more::before,
    .products-card--more::after {
      left: 87px;
    }

    .catalog-products__item--gray {
      padding: 0 30px;
      box-sizing: border-box;
    }

    .products-card {
      width: 245px;

      .products-card__sub-title {
        margin-top: 161px;
      }
    }
  }
}
